* {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;

  text-transform: uppercase;
  letter-spacing: 2.7px;
  line-height: 18px;
  

}
html {
  height: 100%;
  scroll-behavior: smooth;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
#root {
  height: 100vh;
}
a {
  text-decoration: none;
  color: black;
}

a:hover {
  color: #66FCF1;
}

.container {
  margin-right: 0;
  transition: 0.5s;

}
.content {
  margin-left: 0;
  margin-right: 0;
  transition: 0.5s;
  height: 80%;
  width: 100%;
}
.navbar {
  color: #ebebeb;
  margin: 0px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 50px;
  height: 10%;
  background-color: #768285;

}
.main-mobile {
  padding-top: 10vh;
}
.navbar-icon {
  position: absolute;
  left: 5vw;
}

.menu-icon-container {
  z-index: 100;
  width: 100%;
  height: 10vh;
  display: flex;
  position: fixed;
  justify-content: flex-end;
  align-items: center;
  background-color: #768285;

}

.link {
  font-size: 25px;
  margin-right: 25px;
  
  
}

.link:hover {
  color: white
}


.youtube { 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-image: linear-gradient(#768285, #DDDEE0, #768285);
  align-content: center;

}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(#768285, #DDDEE0, #768285);
  align-content: center;
}
.slick-list {
  height: 100%;
  width: 100%;
}


.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-image: linear-gradient(#768285, #DDDEE0, #768285);
  align-content: center;

}

.contact-form {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
  flex-direction: column;
  height: 600px;
  min-height: 30%;
  max-height: 90%;
  width: 500px;
  min-width: 35%;
  max-width: 100%;
  background-color:#fafafa;
  border: 2px solid #eaeaea;
  border-radius: 5px;
  

  
}
.contact-form h2 {
  color: black;
 

}


ul {
  list-style: none;
}
 .inputs {
  list-style-type: none;
  list-style: none;
  padding: 0;
  width: 450px;
  min-width: 50%;
  max-width: 95%;
  height: 50px;
  min-height: 4%;
  max-height: 4%;
  margin-bottom: 20px;

  
}
.comments-input {
  list-style-type: none;
  list-style: none;
  padding: 0;
  height: 200px;
  min-height: 20%;
  max-height: 20%;
  width: 400px;
  min-width: 50%;
  max-width: 95%;
  resize: none;
  color: black;
  margin-bottom: 20px;
}

.form-label {
  font-family: 'Lucida Grande';
  color: black;
  display: block;
  clear: both;
  padding-bottom: 5px;
  
}
.error-message {
  height: 50px;
  color: #FD595B;
  margin-bottom: 20px;
  
}
.contact-form p {
  color: black;
  
 
}
.submit-button {
  
  color: #FFFDFF;
  background-color: #4A9DE3;
  width: 170px;
  min-width: 50%;
  max-width: 100%;
  height: 40px;
  min-height: 50%;
  max-height: 100%;
  box-sizing: border-box;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(216,216,216);
  border-radius: 6px;
  text-align: center;
  
}

.fade-in {animation:fading 2s}

@keyframes 
  fading{ 
      0%{opacity:0.75}
      100%{opacity:1}}

.drawer {
  
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 10vh;
  right: 0;
  background-color: #768285;
  overflow: hidden;
  padding-top: 35px;
  transition: 0.5s;
 
}

.slide-image {
  height: 80%;
  width: 100%;

}
.drawer-link {
  
  
  list-style: none;
  margin-top: 10px;
  font-size: 20px;
  margin-right: 10px;
  margin-left: -20px;

}
.slick-slide, .slick-active, .slick-current {
  height: 100%;
  width: 100%;
}
.slick-list div div {
  height: 100%;
  width: 100%;
}
.slick-track {
  height: 100%;
  width: 100%; 
}

.drawer-close-button { 
  width: 25px;
  position: absolute;
  top: 0;
  right: 0;
  justify-content: flex-end;
  background-color: white;
  padding-right: 10px;
  padding-left: 10px;
  font-size: 45px;

}


a .home {
  padding-top: 10vh;
}

.icon {
  padding-right: 40;
  
}
.material-icons {
  color: black;
  font-size: 45px;
  margin-right: 15px;
 
}

.arrow-prev, .arrow-next {
  position: absolute;
  top: 50%
}
.arrow-prev {
  left: -15px;
}
.arrow-next { 
  right: -35px;
}
.hamburger {
  background-color: black;
  font-size: 45px;
  margin-right: 15px;
}
.footer {
  color: white;
  font-size: 12px;
  background-color: #768285;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;

}
